.s-develop {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 783px;
    padding: 148px 0 75px;
    background: url('.././img/s-develop/bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: 1699px) {
        min-height: 748px;
        padding: 80px 0 68px;
    }

    @media (max-width: 1199px) {
        min-height: 797px;
        padding: 85px 0 65px;
        background-size: auto;
        background-position: 40% -180%;
    }

    @media (max-width: 991px) {
        min-height: 900px;
        padding: 65px 0 60px;
        background-position: 50% 30%;
    }

    @media (max-width: 600px) {
        min-height: 770px;
        background-size: 350%;
        background-position: 50% 10%;
        padding: 41px 0 50px;
    }

    @media (max-width: 359px) {
        min-height: 840px;
    }

    .l-default {
        position: relative;
        display: flex;
        flex-direction: column;
        flex: 1 1 auto;
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: auto;
            min-height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }

    &__title {
        font-size: 40px;
        line-height: 120%;
        font-weight: 300;
        letter-spacing: 1.03em;
        text-transform: uppercase;
        color: #656669;
        margin-bottom: 30px;

        @media (max-width: 1199px) {
            font-size: 35px;
            margin-bottom: 25px;
        }

        @media (max-width: 600px) {
            font-size: 19px;
            margin-bottom: 10px;
        }
    }

    &__top {
        @media (max-width: 600px) {
            max-width: 255px;
            margin: 0 auto;
        }
    }

    &__subtitle {
        font-size: 25px;
        line-height: 120%;
        color: #656669;

        @media (max-width: 1199px) {
            font-size: 21px;
        }

        @media (max-width: 991px) {
            font-size: 17px;
        }

        @media (max-width: 600px) {
            font-size: 12px;
        }

        span {
            line-height: 120%;
            letter-spacing: 0.03em;
            color: #fff;
        }
    }

    &__docs {
        display: flex;
        align-items: center;
        margin-top: auto;

        @media (max-width: 1199px) {
            flex-wrap: wrap;
            justify-content: space-between;
            margin: auto -15px 0;
        }

        @media (max-width: 600px) {
            flex-direction: column;
            align-items: flex-start;
            margin: auto 0 0;
        }

    }

    &__doc {
        display: flex;
        align-items: center;
        margin-right: 70px;

        @media (max-width: 1699px) {
            margin-right: 40px;
        }

        @media (max-width: 1199px) {
            width: calc(50% - 30px);
            max-width: 366px;
            margin: 0 15px 34px;

            &:last-child, &:nth-child(3) {
                margin-bottom: 0;
            }
        }

        @media (max-width: 600px) {
            min-width: 100%;
            margin: 0 0 30px;
            justify-content: space-between;

            &:nth-child(3) {
                margin-bottom: 30px;
            }
        }

        &:last-child {
            margin-right: 0;

            @media (max-width: 1199px) {
                margin-right: 15px;
            }

            @media (max-width: 600px) {
                margin-right: 0;
            }
        }

        &-text {
            font-size: 19px;
            line-height: 120%;
            color: #4E5056;
            margin-right: 15px;

            @media (max-width: 1699px) {
                font-size: 15px;
            }

            @media (max-width: 991px) {
                margin-right: 8px;
            }
        }

        &-btn {
            min-width: 167px;
            padding: 12px 0;
        }
    }
}
